#site-wrapper {
    position: relative;
    height: 5000px;
    width: 100%;

}

#site-canvas {
    width: 100%;
    height: 100%;
    position: relative;
    @include prefix(transform, translate(0), moz webkit ms o);
    @include prefix(transition, .3s ease all, moz ms webkit o);

    & a.toggle-nav{
      position: absolute;
      top: ($logo-heigth/2)-35px;
      left: -30px;
      z-index: 1;
    }
}

.content{
  background: $content-background-color;
  padding-top: 100px;
}

.statistics-cloud{
  margin-top: 100px;
  margin-bottom: 100px;
}
